<template>
	<div class="v-homeland-furniture" v-loading="loading">
		<div class="m-navigation">
			<el-button class="u-goback" size="medium" icon="el-icon-arrow-left" @click="goBack" plain>返回列表</el-button>
			<div class="u-links" v-if="other_id">
				<a class="u-link u-item" :href="getLink('item', item_id)" target="_blank"><i class="el-icon-collection-tag"></i>物品信息</a>
			</div>
		</div>

		<div class="m-info-box">
			<div class="u-img">
				<img :src="formatImg(data.Path)" :alt="data.szName" />
			</div>

			<div class="u-info">
				<div class="u-info-title">
					{{ data.szName }}
					<i class="u-interact" v-if="data.bInteract"></i>
				</div>
				<div class="u-attrs">
					<span class="u-attr" v-if="data.Attribute1"><span class="u-label blue">观赏</span>{{ data.Attribute1 }}</span>
					<span class="u-attr" v-if="data.Attribute2"><span class="u-label pink">实用</span>{{ data.Attribute2 }}</span>
					<span class="u-attr" v-if="data.Attribute3"><span class="u-label yellow">坚固</span>{{ data.Attribute3 }}</span>
					<span class="u-attr" v-if="data.Attribute4"><span class="u-label green">风水</span>{{ data.Attribute4 }}</span>
					<span class="u-attr" v-if="data.Attribute5"><span class="u-label purple">趣味</span>{{ data.Attribute5 }}</span>
				</div>
				<div class="u-metas">
					<span class="u-meta"><span class="u-label">来源途径：</span>{{ data.szSource }}</span>
					<span class="u-meta"><span class="u-label">园宅等级：</span>{{ data.LevelLimit }}级</span>
					<span class="u-meta"><span class="u-label">摆放上限：</span>{{ data.MaxAmountPerLand }}</span>
					<span class="u-meta u-meta-scale" v-if="data.szScaleRange"
						><span class="u-label">缩放大小：</span>
						<span class="u-value">
							<b v-for="item in scaleRange(data.szScaleRange)" :key="item">{{ item }}</b>
						</span>
					</span>
					<span class="u-meta u-meta-dyes" v-if="color_list.length"
						><span class="u-label">染色选项：</span> <span class="u-value"> <i v-for="item in color_list" :key="item" class="u-dye" :style="{ backgroundColor: `rgb(${item})` }"></i> </span
					></span>
				</div>

				<div class="m-buttons">
					<!-- 收藏按钮 -->
					<Fav class="u-collect" post-type="furniture" :post-id="id" />
				</div>
			</div>
		</div>

		<div class="m-extend" v-if="data.szTip || setData">
			<div class="m-extend-header"><svg xmlns="http://www.w3.org/2000/svg" width="31.531" height="31.531" viewBox="0 0 31.531 31.531" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M11.872 24.961l-2.539.412a2.59 2.59 0 01-1.961-.468 2.606 2.606 0 01-1.043-1.72l-.223-1.482-4.407 4.407a.835.835 0 00.438 1.413l2.801.523.523 2.801a.838.838 0 001.413.438l5.755-5.755-.422-.427a.471.471 0 00-.335-.142z"/><path d="M19.91 23.932l2.458.404a1.554 1.554 0 001.794-1.303l.37-2.458c.075-.492.383-.917.829-1.141l2.224-1.111a1.564 1.564 0 00.686-2.111L27.129 14a1.518 1.518 0 010-1.403l1.144-2.211c.19-.37.227-.802.098-1.199a1.57 1.57 0 00-.785-.911l-2.223-1.112a1.53 1.53 0 01-.829-1.14l-.37-2.458a1.556 1.556 0 00-.624-1.026 1.555 1.555 0 00-1.17-.279l-2.457.405a1.52 1.52 0 01-1.335-.434L16.828.459A1.568 1.568 0 0015.766 0a1.566 1.566 0 00-1.061.459l-1.75 1.773a1.52 1.52 0 01-1.335.434l-2.457-.405a1.554 1.554 0 00-1.17.279 1.556 1.556 0 00-.624 1.026l-.37 2.458a1.532 1.532 0 01-.83 1.14L3.947 8.276a1.568 1.568 0 00-.785.912c-.129.396-.093.829.097 1.199l1.144 2.211c.229.439.229.964 0 1.403l-1.143 2.21a1.563 1.563 0 00.687 2.111l2.224 1.111c.446.224.753.649.829 1.141l.37 2.458c.062.412.288.783.624 1.026a1.55 1.55 0 001.17.277l2.458-.404c.489-.082.987.08 1.335.436l1.75 1.771a1.55 1.55 0 001.059.459 1.55 1.55 0 001.059-.459l1.75-1.771a1.517 1.517 0 011.335-.435zm-4.144-1.39c-5.205-.026-9.431-4.266-9.431-9.477 0-5.21 4.226-9.451 9.431-9.478 5.205.026 9.432 4.268 9.432 9.478-.001 5.211-4.227 9.451-9.432 9.477z"/><path d="M15.766 4.955c-4.444.028-8.05 3.648-8.05 8.097 0 4.447 3.606 8.068 8.05 8.096 4.444-.027 8.05-3.648 8.05-8.096-.001-4.448-3.606-8.069-8.05-8.097zM29.833 26.11l-4.407-4.407-.223 1.482a2.615 2.615 0 01-3.004 2.188l-2.539-.412a.468.468 0 00-.335.143l-.422.427 5.755 5.755a.838.838 0 001.413-.438l.522-2.801 2.801-.523a.837.837 0 00.439-1.414z"/></svg> <span class="u-txt">家具信息</span></div>
			<div class="m-extend-desc" v-if="data.szTip">
				<div class="u-title"><i class="el-icon-star-on"></i>家具介绍</div>
				<div class="u-txt" v-html="description_filter(data.szTip)"></div>
			</div>
			<div class="m-extend-relation" v-if="setData">
				<div class="u-title"><i class="el-icon-star-on"></i>{{ setData.szName }}<el-rate class="u-star" v-model="setData.nStars" disabled></el-rate></div>
				<furnitureSet :data="setData" />
			</div>
		</div>

		<!-- 攻略 -->
		<div class="m-furniture-wiki">
			<Wiki source_type="item" :source_id="item_id" :type="type" :id="id" title="家具攻略"></Wiki>
		</div>

		<!-- 评论 -->
		<div class="m-furniture-comment">
			<Comment :id="id" :category="type" order="desc" />
		</div>
	</div>
</template>

<script>
import { getLink } from "@jx3box/jx3box-common/js/utils";
import { getFurnitureDetail, getSetList, getFurnitureColor } from "@/service/furniture.js";
import { __iconPath } from "@jx3box/jx3box-common/data/jx3box.json";
import furnitureSet from "@/components/homeland/furniture_set.vue";
import Wiki from "@/components/wiki/Wiki.vue";
import Comment from "@jx3box/jx3box-comment-ui/src/Comment.vue";

import { postStat } from "@jx3box/jx3box-common/js/stat.js";

export default {
	name: "FurnitureSingle",
	props: [],
	components: {
		Wiki,
		Comment,
		furnitureSet,
	},
	data: function () {
		return {
			type: "furniture",
			loading: false,
			data: "", // 家具数据
			setData: "",
			colorData: "", // 染色数据
		};
	},
	computed: {
		id: function () {
			return this.$route.params.id;
		},
		other_id: function () {
			return this.data?.__manufactureID;
		},
		item_id: function () {
			return "10_" + this.data?.__manufactureID;
		},
		achievement_id: function () {
			return this.petWiki?.achievement_id;
		},
		represent_id: function () {
			return this.data?.nRepresentID;
		},
		set_id: function () {
			return this.data?.SetID;
		},

		color_list: function () {
			let list = [];

			for (const key in this.colorData) {
				if (key.startsWith("szDetailIndex")) {
					this.colorData[key] && list.push(this.colorData[key].replace(/;/g, ","));
				}
			}
			return list;
		},
	},
	watch: {
		id : function (){
			this.getData()
		}
	},
	methods: {
		// 数据加载
		// ==============
		getData() {
			this.loading = true;
			getFurnitureDetail(this.id)
				.then((res) => {
					this.data = res.data;

					// 发送统计
					postStat(this.type, this.id);
				})
				.then(() => {
					this.represent_id && this.getColorData();
					this.set_id && this.getSetData();
				})
				.finally(() => {
					this.loading = false;
				});
		},
		getColorData() {
			getFurnitureColor(this.represent_id).then((res) => {
				this.colorData = res.data;
			});
		},
		getSetData() {
			getSetList(this.set_id).then((res) => {
				this.setData = res.data;
			});
		},

		// 工具函数
        // ===================
		getLink,
		// 描述过滤
		description_filter(value) {
			let matchs = /text="(.*?)(\\\\\\n)?"/.exec(value);
			if (matchs && matchs.length > 1) value = matchs[1].trim();
			if (value) value = value.replace(/\\n/g, "<br>");
			return value;
		},
		// 图片链接转换
		formatImg(link) {
			if (!link) return;
			let img = link.match(/.*[\/,\\](.*?).tga/);
			link = link.replace(/\\/g, "/").split(img[1]);
			let nLink = link[0].split("Homeland");

			if (img[1] == "default") return __iconPath + "pvx/furniture/default/default.png";
			return __iconPath + "pvx/furniture" + nLink[1] + img[1] + ".png";
		},

		scaleRange(str) {
			return str?.split(";");
		},

		goBack() {
			this.$router.push({ name: "list" });
		},
	},
	created: function () {
		this.getData();
	},
};
</script>
<style lang="less">
@import "~@/assets/css/homeland/single.less";
</style>
